/* Funcin que convierte los milisegundos a formato minutos:segundos */
function convertMsToMm(ms) {
    const min = Math.floor(ms / 60000)
    const sec = Math.floor(ms % 60000 / 1000).toFixed(0) // toFixed(0) quita los decimales redondeando hacia arriba

    return `${min}:${sec}`
}

const msToMm = {}

/* Recibo la instancia de Vue por parmetro */
msToMm.install = function(Vue) {
    /* filter() me permite crear un filtro, recibe el nombre que tendr el filtro y la funcin a ejecutar */

    /* Este filtro recibir el valor inicial y devolver el valor convertido */
    Vue.filter('ms-to-mm', (val) => {
        return convertMsToMm(val)
    })
}

export default msToMm


