/* Este archivo crear el servicio que interactuar con la API de info-series */

/* Si quisieramos interactuar con otras API como las de Twitter,Facebook,Github...  deberamos crear un archivo
para cada servicio para cada una y as poder centralizar las llamadas y acceder a esas llamadas desde su propio servicio */

/* Importamos la librera trae y el archivo de configuracin de servicios.
Cuando no pongo ruta en el import se refiere a una librera de npm, webpack lo identifica por mi */
import trae from 'trae'
import configService from './config.js'

/* Este objeto ser para crear una nueva instancia de la librera trae.
El mtodo que permite la creacin es create() y baseUrl har referencia a nuestra
API de config.js para que no estemos repitiendola en otros archivos */
const infoSeriesService = trae.create({
    baseUrl: configService.apiUrl
})

/* Exportamos el servicio para poder usarlo desde fuera */
export default infoSeriesService;


