/* Funcin que convierte dlares en euros */
function valorEuros(precio) {
    return `${precio * 0.81}`
}

const convertDolarEuros = {}

/* Recibo la instancia de Vue por parmetro */
convertDolarEuros.install = function(Vue) {
    /* filter() me permite crear un filtro, recibe el nombre que tendr el filtro y la funcin a ejecutar */
    /* Este filtro recibir el valor en dlares y lo devolver en Euros */
    Vue.filter('dolar-a-euros', (precio) => {
        return valorEuros(precio)
    })
}

/* Lo exportamos */
export default convertDolarEuros


